body {
  color: #fff;
  font-family: 'Roboto', 'Lato', 'Helvetica', 'Arial', sans-serif;
  font-size: 0.875rem;
  line-height: 1.5;
  margin: 1rem;
}
table {
  border-collapse: collapse;
  border-radius: 4px;
  border-style: hidden;
  box-shadow: 0 0 0 1px #202626;
}
table th,
table td {
  border: 1px solid #202626;
  padding: 0.5rem 0.75rem;
}
figure {
  display: table;
  margin: 1rem auto;
}
figure figcaption {
  color: #999;
  display: block;
  margin-top: 0.25rem;
  text-align: center;
}
hr {
  background: #202626;
  border: 0;
  height: 1px;
  margin: 1rem 0;
}
code {
  background-color: #202626;
  border-radius: 4px;
  padding: 0.1rem 0.2rem;
}
